@keyframes bounce-around {
	0% {
		transform: scale(1.1);
	}
	25% {
		transform: scale(1.2);
	}
	50% {
		transform: scale(1.1);
	}
	75% {
		transform: scale(1.2);
	}
	100% {
		transform: scale(1.1);
	}
}
.main__header {
	position: relative;
}
.main__header .hamburger {
	position: absolute;
	right: 22px;
	top: 15px;
	display: flex;
	flex-direction: column;
	transform: translate(50%, 0);
	transition: all 500ms ease-in-out;
}
.main__header .hamburger div {
	border: 1px solid white;
	width: 25px;
	margin: 3px;
}
