	.main__gallery img {
		width: 20%;
	}
}
.main__gallery .jump {
	animation: jump;
	animation-timing-function: ease-in-out;
	animation-duration: 10s;
	animation-iteration-count: infinite;
	animation-fill-mode: forwards;
}

@keyframes jump {
	0% {
		transform: translateY(0px);
	}
	25% {
		transform: translateY(10px);
	}
	50% {
		transform: translateY(-10px);
	}
	75% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(0px);
	}
}
@keyframes shake {
	0% {
		color: rgb(0, 0, 0);
	}
	25% {
		color: #497469;
	}
	50% {
		color: rgb(0, 0, 0);
	}
	75% {
		color: #497469;
	}
	100% {
		color: rgb(0, 0, 0);
	}
}
@keyframes flip {
	0% {
		transform: translateX(30px);
	}
	25% {
		transform: translateX(-30px);
	}
	50% {
		transform: translateX(30px);
	}
	75% {
		transform: translateX(-30px);
	}
	100% {
		transform: translateX(30px);
	}
}
